:root {
  --purple: hsl(256, 26%, 20%);
  --grayish-blue: hsl(216, 30%, 68%);
  --dark-purple: hsl(270, 9%, 17%);
  --gray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --grayish-purple: hsl(273, 4%, 51%);
}

body {
  font-size: 1rem;
  margin: 0;
}

h1 {
  margin: 0;
  font-size: 3rem;
  font-weight: 400;
  font-family: "DM Serif Display";
  line-height: 3.4rem;
  text-wrap: wrap;
  width: 22rem;
  padding-bottom: 1rem;
  padding-top: 1.4rem;
}

h1::before {
  content: "";
  margin: 0;
  display: block;
  height: 1.4rem;
  width: 6.5rem;
  background-color: var(--dark-purple);
  border-top: 0.02rem solid var(--white);
}

h2 {
  font-size: 1.7rem;
  font-weight: 400;
  font-family: "DM Serif Display";
}

h3 {
  font-size: 1.6rem;
  font-weight: 400;
  font-family: "DM Serif Display";
}

h4 {
  font-size: 1.4rem;
  font-weight: 400;
  font-family: "DM Serif Display";
}

nav {
  font-family: "Karla";
  font-size: 0.7rem;
  font-weight: 400;
}

.human {
  background-color: var(--dark-purple);
  color: var(--white);
  font-family: "Karla";
  font-size: 0.67rem;
  font-weight: 100;
  line-height: 1rem;
  height: 30rem;
  padding-left: 4rem;
  padding-top: 3.6rem;
}

.containern {
  margin: 0;
  width: 23.5rem;
}

.button a {
  border: 0.1rem solid var(--purple);
  padding: 0.4rem 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--purple) !important;
}

.button a:hover {
  background-color: var(--purple);
  color: var(--white) !important;
  transition: 0.3s ease-out;
  cursor: pointer;
}

.navmenu {
  display: flex;
  align-items: center;
  gap: 1rem;
  text-transform: uppercase;
}
.navmenu a {
  text-decoration: none;
  color: var(--grayish-purple);
}
.navmenu :hover {
  color: var(--purple);
  transition: 0.3s;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 4rem;
  padding-right: 4rem;
  min-width: 40rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}